        </div>

        <div class="wz">
          老班章地区，单是干茶与往年价格相比，几乎上涨了4成以上。新闻君获悉，今年的头春成品茶价格或会翻涨60%—70%。往年同期，可以手握50公斤左右头春大树茶的茶客，今年到手的茶叶可能10公斤都不到。
        </div>

        <div class="img">
          <img :src="img3" alt="" />
        </div>

        <div class="wz">
